.frozen {
    backdrop-filter: blur(4px);
}

.image-container {
    // padding-bottom: 60%;

    &.-rounded {
        border-radius: 10px;
    }

    &.-bordered {
        display: inline-block;
        margin: auto;
        padding: .5rem;
        background-color: var(--color-white);
        border: 1px solid var(--color-grey);
        border-radius: var(--standard-border-radius);
        text-align: center;
    }
}