.Toolbar {
  width: 100%;
  height: var(--toolbar-height);
  position: fixed;
  top: 0;
  left: 0;
  background-color: var(--color-brown);
  display: flex;
  justify-content: space-between;
  padding: 0 20px;
  z-index: 90;
  align-items: center;
  box-sizing: border-box;
}

.Toolbar nav {
  height: 100%;
}

.Toolbar > .DrawerToggleButton > div {
  background-color: var(--color-white);
}

@media (max-width: 500px) {
  .Toolbar > nav {
    display: none;
  }
}
