@use '../../../../styles//mixins//respond' as *;

.List {
    background-color: var(--color-white);
    padding: var(--spacing-half);
    margin: auto;
    counter-reset: list-counter;
    display: flex;
    flex-direction: column;

    @include respond(mobile-large desktop) {
        max-width: 80%;
    }

    > .list-item {
        font-size: var(--fs-lg);
        padding: 10px 20px;

        &.-selected {
            background-color: rgba(25, 118, 210, 0.08);
        }

        &.-with-divider {
            border-bottom: 1px solid rgba(0, 0, 0, .12);
        }
    
        &:hover {
            background-color: rgba(0, 0, 0, 0.04);
        }
    
        &::before {
            content: counter(list-counter, upper-latin);
            counter-increment: list-counter;
            margin: 10px;
            display: block;
            width: 30px;
            height: 30px;
            text-align: center;
            line-height: 30px;
            border-radius: 50%;
            background-color: var(--color-white);
            color: initial;

            @include respond(mobile-large desktop) {
                display: inline-block;
            }
        }

        > * {
            vertical-align: middle;
        }

        .sub-title {
            font-size: var(--fs-sm);
            opacity: .6;
        }

        > a {
            @include respond(mobile-medium mobile-large desktop) {
                width: calc(100% - 50px);
            }
        }
    }

    a:hover {
        text-decoration: none;
    }
}

