.InputContainer {
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  overflow: hidden;
  position: relative;
}
.Label {
  font-weight: 900;
  display: block;
  margin: 8px;
  position: relative;
}

.Input {
  outline: none;
  border: 1px solid var(--color-grey);
  border-radius: var(--standard-border-radius);
  padding: 6px 10px;
  display: block;
  width: 100%;
}

.Input:focus {
  background: var(--color-light-grey);
}

.Invalid {
  border: 1px solid var(--color-red);
  background: #fda49a;
}

.Valid {
  border: 1px solid var(--color-green);
  background-color: aquamarine;
}

.validation-error {
  color: var(--color-red);
  margin: 0 5px;
}
