.outline {
    color: var(--color-white);
    -webkit-text-stroke: 2px var(--color-black);
}

.text-justify {
  text-align: justify;
}

.blockquote {
  position: relative;
  padding-left: var(--spacing-full);
  border-left: 5px solid var(--color-blue);
  font-family: Roboto;
  font-size: var(--fs-h4);
  line-height: var(--spacing-full);
  font-weight: var(--fw-light);

  > blockquote {
    &::before {
      content: '\201C';
      color: var(--color-blue);
      vertical-align: middle;
      font-size: 4rem;
      margin-right: 10px;
    }
  }

  figcaption {
    text-align: right;
    font-size: var(--fs-h5);
    color: var(--font-pale);
    padding-top: var(--spacing-quarter);
    margin: var(--spacing-quarter) 0 24px;
  }
}

article p {
  color: var(--font-bold);
  font-size: var(--fs-lg);
  font-weight: var(--fw-medium);
  line-height: 1.75;
  margin: var(--spacing-quarter) 0 24px;
  letter-spacing: .56px;
  text-align: left;
}

.first-letter-styled::first-letter {
  color: var(--color-black);
  float: left;
  font-family: "Lora",serif;
  font-size: 4rem;
  font-stretch: condensed;
  line-height: .66;
  margin-left: 4px;
  margin-right: 20px;
  margin-top: 12px;
  text-align: center;
  text-transform: uppercase;
}

::selection {
  background-color: var(--color-light-blue);
}

@for $i from 1 through 6 {
    h#{$i} {
      font-size: var(--fs-h#{$i});
    }
}