// @include respond;
@use '../../../../styles/mixins/position' as *;

// Colors
$primary: #f1c40f;
$accent: #46627f;
$white: whitesmoke;
$font-monospace: monospace;

.Card {
    min-width: 150px;
    min-height: 50px;
    margin: 5px;
    box-shadow: 0 2px 3px var(--color-grey);
    background: var(--color-white);
    padding: 10px 20px;
    cursor: pointer;
    text-align: center;

	&.-selected {
		border: 2px solid var(--color-blue);
	}

	&.-match {
		position: relative;
		animation: hideMatchedCards 1.7s ease forwards;
	}
  }

.BlogCard {
	font-family: "Source Sans Pro", sans-serif;
    margin: var(--spacing-half);
    overflow: hidden;
    position: relative;
	display: flex;
	align-items: flex-start;
	justify-content: center;
	width: 290px;
	height: 395px;
	padding: 20px;
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
	transition: all 0.5s cubic-bezier(.25,.8,.25,1);

    &:before {
        @include absolute-same(0);

		content: '';
		background-color: rgba(black,0.2);
		z-index: 1;
	}

    &:not(.-mobile):hover {
        &::before {
            background-color: rgba(black, 0);
        }

        > .img-holder {
            transform: scale(1.2);
        }

        > .date {
			bottom: -20%;
		}

		> figcaption {
			transform: translateY(-150%);
		}
    }

    > .img-holder {
        @include absolute-same(0);

        transform: scale(1);
        transition: transform .5s linear;
        
        > img {
            object-fit: cover;
            object-position: center;
            width: 100%;
            height: 100%;
        }
    }

    > .date {
		position: absolute;
		bottom: 0;
		right: 30px;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		padding: 10px;
		background-color: rgba($primary,0.8);
		text-shadow: 1px 1px rgba(black,0.7);
		transition: all 0.5s cubic-bezier(.25,.8,.25,1);
        z-index: 2;

		> span {
			color: white;
			line-height: 1;
			&:first-child {
				font-family: $font-monospace;
				font-size: 20px;
				font-weight: 900;
			}
			&:last-child {
				font-size: 14px;
				font-weight: 400;
			}
		}
	}

	> .author {
		color: var(--color-white);
		position: relative;
	}

	> figcaption {
		color: var(--color-white);
		transition: all 0.5s cubic-bezier(.25,.8,.25,1);;
		z-index: 2;
		> .title {
			margin: 0 0 5px;
			font-family: $font-monospace;
			font-size: 24px;
			line-height: 1.35;
			text-shadow: 1px 1px rgba(black,0.7);

            > span {
                background-color: rgba($primary,0.8);
            }
		}

		> .short-description {
			margin: 0;
			line-height: 1.5;
			text-shadow: 1px 1px rgba(black,0.85);
		}
	}
}

.ExampleCard {
	border-radius: var(--standard-border-radius);
	background-color: var(--color-light-grey);
	padding: var(--spacing-full) var(--spacing-half);
	margin-bottom: var(--spacing-half);
	text-align: center;

	> .example {
		line-height: 1.83;
		font-size: var(--fs-h5);
	}

	> .translation {
		color: var(--color-green);
	}

	> span {
		display: block;
	}
}

@keyframes hideMatchedCards {
    from {
      left: 0;
    }
    to {
      left: -200%;
    }
  }