.SelectCorrectOption {
    li {
        list-style: decimal;
        font-size: var(--fs-lg);
        margin-bottom: var(--spacing-half);
    }

    select {
        min-width: 50px;
        font-size: var(--fs-lg);
    }
}

.-wrong {
    border: 3px solid var(--color-red);
}

.-correct {
    border: 3px solid var(--color-green);
}