.FillGaps {
    box-shadow: 2px 3px 14px 0px var(--color-black);
    background-color: var(--color-white);
    max-width: 648px;
    margin: auto;
    padding: var(--spacing-full);

    ol {
        list-style: decimal;

        > li {
            margin-bottom: 10px;

            &.-correct,
            &.-wrong {
                &::after {
                    content: '';
                    display: inline-block;
                    width: 30px;
                    height: 30px;
                    vertical-align: middle;
                    margin-left: 10px;
                }
            }
        }
    }
}

.FillGaps ol li.-wrong::after {
    background: url(../../../../static_assets/images/wrong.png) center / cover;
}
.FillGaps ol li.-correct::after {
    background: url(../../../../static_assets/images/tick.png) center / cover;
}
.FillGaps ol li .InputContainer {
    width: auto;
    display: inline-block;
    vertical-align: middle;
}
.FillGaps ol li .InputContainer .Label {
    margin: 0;
}
.FillGapsControls {
    text-align: center;
}