@use '../../../../../styles/mixins/respond' as *;

$colorRed: #f00;
$colorRedPale: #fa5a5a;
$colorYellowPale: #f0d264;
$colorGreenPale: #82c8a0;
$colorCyanPale: #7fccde;
$colorBluePale: #6698cb;
$colorPurplePale: #cb99c5;

$buttonColors: (
	'red-pale': $colorRedPale,
	'yellow-pale': $colorYellowPale,
	'green-pale': $colorGreenPale,
	'cyan-pale': $colorCyanPale,
	'blue-pale': $colorBluePale,
	'purple-pale': $colorPurplePale,
);

a[class*="Button"] {
	text-decoration: none;
}
input[class*="Button"], 
button[class*="Button"] {
	border: 0;
}

.Button {
  background-color: transparent;
  border: none;
  color: var(--color-white);
  display: inline-block;
  outline: none;
  cursor: pointer;
  font: inherit;
  padding: 10px;
  position: relative;
  margin: 10px;
  font-weight: bold;
  text-align: center;

  &.btn-3d {
	display: inline-block;
	font-size: var(--fs-lg);
	padding: 20px;
	margin: 10px;
	border-radius: 6px;
	transition: top .01s linear;
	text-shadow: 0 2px 0 rgba(0,0,0,.25);

	&:active {
	  top: 9px;
	}
  }

  &.-with-shadow {
	color: var(--color-white);	
	padding: 15px 25px;
	display: inline-block;
	border: 1px solid rgba(0,0,0,0.21);
	border-bottom-color: rgba(0,0,0,0.34);
	text-shadow:0 1px 0 rgba(0,0,0,0.15);
	box-shadow: 0 1px 0 rgba(255,255,255,0.34) inset, 
				      0 2px 0 -1px rgba(0,0,0,0.13), 
				      0 3px 0 -1px rgba(0,0,0,0.08), 
				      0 3px 13px -1px rgba(0,0,0,0.21) !important;

	&:active {
		top: 1px;
		border-color: rgba(0,0,0, .34) rgba(0,0,0, .21) rgba(0,0,0, .21);
		box-shadow: 0 1px 0 rgba(255,255,255, .89),0 1px rgba(0,0,0, .05) inset !important;
		position: relative;
	}
}

&.-with-gradient {
	text-decoration: none;
	color: var(--color-white);
	padding: 10px 30px;
	display: inline-block;
	position: relative;
	border: 1px solid rgba(0,0,0,0.21);
	border-bottom: 4px solid rgba(0,0,0,0.21);
	border-radius: 4px;
	box-shadow: none !important;
	text-shadow: 0 1px 0 rgba(0,0,0,0.15);
}

  &.-primary {
    background-color: var(--color-blue);
    color: var(--color-white);
    border-radius: var(--standard-border-radius);
  }

  &.-secondary {
    color: var(--color-white);
    border-radius: var(--standard-border-radius);
    background-color: var(--color-green);
  }

  &.-plain {
	padding: 10px 20px;
	border: 1px solid #ccc;
	color: inherit;
	font-weight: normal;
  }

  &.-pulse {
    background: var(--color-red);
    border-radius: 50%;
    border: none;
    box-shadow: 0 0 0 0 rgba($colorRed, .5);
    height: 70px;
    letter-spacing: -1px;
    margin: auto;
    position: relative;
    text-align: center;
    text-transform: uppercase;
    width: 70px;

    &.-active {
      animation: pulse 1.5s infinite;
    }

    @include respond(mobile-large desktop) {
      width: 100px;
      height: 100px;
    }
  }

  &:hover {
    filter: grayscale(25%);
  }

  &:active {
    top: 4px;	
  }

  &:focus {
	// border: 4px solid transparent;
	// box-shadow: 2px solid var(--color-blue);
	outline-offset: 2px;
	outline: 2px solid var(--color-blue);
  }

  // COLORS
  @each $colorModifier, $colorValue in $buttonColors {
	&.-#{$colorModifier} {
		background-color: $colorValue;
		box-shadow: 0 4px darken($colorValue, 20%);
	}

	&.-#{$colorModifier}:active {
		background-color: lighten($colorValue, 5%);
		box-shadow: 0 0 $colorValue;
	}

	&.btn-3d.-#{$colorModifier} {
		background-color: $colorValue;
		box-shadow: 0 0 0 1px darken($colorValue, 5%) inset,
			0 0 0 2px rgba(255,255,255,0.15) inset,
			0 8px 0 0 darken($colorValue, 20%),
			0 8px 0 1px rgba(0,0,0, .4),
			0 8px 8px 1px rgba(0,0,0, .5);
	}

	&.btn-3d.-#{$colorModifier}:active {
		box-shadow: 0 0 0 1px darken($colorValue, 30%) inset,
					0 0 0 2px rgba(255,255,255,0.15) inset,
					0 0 0 1px rgba(0,0,0,0.4);
	}

	&.-gradient-#{$colorModifier} {
		background-image: linear-gradient(to bottom, $colorValue 0%, darken($colorValue, 20%) 100%);
	}
  }

  // SIZES
	// height
	&.-large {
		padding: 20px 40px; 
		font-size: 1.375rem;
	}

	&.-small {
		padding: 8px 18px;  
		font-size: .875rem;
	}

	&.-mini {
		padding: 4px 12px;  
		font-size: .75rem;
	}

	&.-block {
		display: block;
		width: 60%;
		margin-left: auto;
		margin-right: auto;
	}

	// width
	&.-wide {
		padding-left: 10px;
		padding-right: 10px;
		width: 100%;
	}

	&.-short {
		padding-left: 20px;
		padding-right: 20px;
	}

	&.-rounded {
		border-radius: 10px;
	}
}

.Button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.Success {
  color: #5c9210;
}

.Danger {
  color: #944317;
}

.outline-btn {
	border: 1px solid rgba(255, 255, 255, 0.2);
	color: rgba(255, 255, 255, 0.4);
	font-size: var(--fs-sm);
	text-transform: uppercase;
	cursor: pointer;
	transition: all .2s ease;
	width: 192px;
    height: 46px;
    border-radius: var(--standard-border-radius);
    background-color: transparent;
    margin: 20px;

	@each $colorModifier, $colorValue in $buttonColors {
		&.-#{$colorModifier} {
			border-color: rgba($colorValue, .2);
			color: rgba($colorValue, .4);
		}
	
		&.-#{$colorModifier}:hover {
			border-color: $colorValue;
			color: $colorValue;
		}
	}

	&:hover {
		border-color: var(--color-white);
		color: var(--color-white);
	}
}

@keyframes pulse {
  0% {
    transform: scale(.9);
  }

  70% {
    transform: scale(1);
    box-shadow: 0 0 0 35px rgba($colorRed, 0);
  }

  100% {
    transform: scale(.9);
    box-shadow: 0 0 0 0 rgba($colorRed, 0);
  }
}