div,
span,
h1,
h2,
p,
em,
img,
strong,
b,
u,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
legend,
form,
label,
table,
tr,
th,
td {
	background: transparent;
	border: 0;
	font-size: 100%;
	margin: 0;
	outline: 0;
	padding: 0;
	vertical-align: baseline;
}

template {
	display: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;

	td,
	th {
		vertical-align: top;
	}
}

@media only screen and (orientation: landscape), only screen and (orientation: portrait) {
	body {
		-webkit-text-size-adjust: 100%;
	}
}

iframe {
	border: 0; // instead of frame border
}