:root {
  --christmas-pink: #f5624d;
  --christmas-red: #cc231e;
  --christmas-light-green: #34a65f;
  --christmas-dark-green: #0f8a5f;
  --christmas-blue: #235e6f;
  --christmas-gradient-green: repeating-linear-gradient(
    45deg,
    #2ecc71,
    #2ecc71 10px,
    #27ae60 10px,
    #27ae60 20px
  );
  --christmas-gradient-red: repeating-linear-gradient(
    45deg,
    #e74c3c,
    #e74c3c 10px,
    #c0392b 10px,
    #c0392b 20px
  );
  --christmas-gradient-candy: repeating-linear-gradient(
    45deg,
    #ffffee,
    #ffffee 15px,
    #d33144 15px,
    #d33144 30px
  );
}

