/*PRESENT*/
.present,
.wishCard {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 100;
}
.present {
  cursor: pointer;
  box-sizing: border-box;
  opacity: 1;
  transition: opacity 2s linear;
}
.cube > div {
  width: 100px;
  height: 100px;
  border: 10px solid #0f0;
  background: #f00;
  position: absolute;
  transform-origin: 50% 50% -50px;
  text-align: center;
  line-height: 100px;
  color: #fff;
  font-size: 2rem;
  box-shadow: 0 0 20px 5px gold;
  opacity: 1;
}
.cube {
  position: relative;
  width: 100px;
  height: 100px;
  transform-origin: 50% 50% -50px;
  transform-style: preserve-3d;
  opacity: 1;
  transform: rotateX(-25deg) rotateY(-400deg);
}
.block {
  perspective: 900px;
}
.side-2 {
  transform: rotateY(90deg);
}
.side-3 {
  transform: rotateY(-90deg);
}
.side-4 {
  transform: rotateX(90deg);
}
.side-5 {
  transform: rotateX(-90deg);
}
.side-6 {
  transform: rotateY(180deg);
}

.wishCard {
  padding-top: 20px;
  transform: translate(-50%, -50%);
}
.card {
  width: 180px;
  height: 180px;
  position: relative;
  perspective: 700px;
  float: left;
  margin: 10px;
}
.card:hover .front {
  transform: rotateY(-80deg);
}
.card:hover .back {
  transform: rotateY(20deg);
}
.card:hover .face {
  transition: all 0.3s ease-out;
}
.face {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  transition: all 0.5s ease-out;
  transform-origin: 0 0;
}
.front {
  transform: rotateY(0deg);
  z-index: 2;
  background: #ccc;
  padding: 1rem;
}
.front > span {
  display: block;
  font: 1.2rem cursive;
  margin-bottom: 10px;
}
.back {
  background: #9dcc78;
  transform: rotateY(0deg);
  padding: 1.8rem;
  z-index: 1;
}
.back h2 {
  font-size: 1.3rem;
  text-align: center;
  margin-top: 0;
}
.back h2 > span {
  padding-top: 5px;
  font-size: 1rem;
  display: block;
}
