@use '../../../../styles//mixins/respond' as *;

.Alphabet {
    display: flex;
    flex-direction: column;

    @include respond(mobile-large desktop) {
        flex-flow: row nowrap;           
    }

    > .letters-container {
       background-color: var(--color-grey-pale);
       text-align: center;
    }

    > .words-container {
        flex: 1;
        background-color: var(--color-white);
        padding: 20px;

        > .current-letter {
            color: var(--color-blue);
            font-size: var(--fs-h3);
            font-weight: var(--fw-bold);
            text-align: center;
            margin-bottom: var(--spacing-full);
        }
        
        > .words-list {
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            font-weight: var(--fw-bold);
            gap: 5px;

            > span {
                display: inline-block;
                padding: 10px 0 10px calc(var(--spacing-full) + 10px);
                position: relative;
                cursor: pointer;

                &::before {
                    content: '';
                    position: absolute;
                    left: 0;
                    top: 50%;
                    transform: translateY(-50%);
                    width: var(--spacing-full);
                    height: var(--spacing-full);
                    background: url(../../../../static_assets/images/icons/mic-ic.svg) no-repeat center/contain;
                }
            }

            strong {
                color: var(--color-blue);
            }
        }
    }
}

.letters-wrap {
    display: flex;
    gap: 3px;
    flex-wrap: nowrap;
    flex: 1;
    overflow: auto;
    padding: 20px;
    font-size: var(--fs-lg);
    letter-spacing: 2px;
    width: 300px;
    user-select: none;
    cursor: pointer;

    > .letter-tile {
        background-color: var(--color-white);
        width: 60px;
        min-width: 60px;
        height: 60px;
        line-height: 60px;
        text-align: center;
        border-radius: var(--standard-border-radius);

        &:hover {
            color: var(--color-blue);
        }

        &.-active {
            box-shadow: rgb(0 0 0 / 12%) 3px 0px 10px;
            color: var(--color-explorer-blue);
            font-weight: var(--fw-bold);
        }
    }

    @include respond(mobile-large desktop) {
        flex-wrap: wrap;           
    }
}