:root {
	--main-color: #5faf4b;
	--font-pale: #9a9a9a;
	--font-bold: #0d0e10;
	--main-bg: #f3f3f3;
	--old-paper-bg: #efdfbb;
	--letters-bg: #59baf1;
	--grey-bg: #f6f6f6;
	--color-white: #fff;
	--color-light-blue: #d1e5f0;
	--color-blue: #2582e7;
	--color-explorer-blue: #5471b3;
	--color-black: #000;
	--color-grey: #bbb;	
	--color-light-grey: #dadada;
	--color-dark-grey: #757575;
	--color-grey-pale: #f4f6f7;
	--color-light-green: #ebf4fd;
	--color-green: #03ac13;
	--color-green-pale: #dcf5eb;
	--color-red:#ff2800;
	--color-red-pale: #fce4df;	
	--color-gold: #f9dc00;
	--color-silver: #999;
	--color-brown: #703b09;
	--color-chocolate: #8f5c2c;
	--color-purple: #903779;
	--bg-social: #eceeef;
	--bg-facebook: #3b5998;
	--bg-youtube: #00aced;
	--bg-linkedin: #007bb6;
	--bg-instagram: #ea4c89;
	--footer-bg: #37383c;
	--footer-text-color: #737373;
	--border-color: #e6e9ee;
	--spacing-full: 32px;
	--spacing-half: calc(var(--spacing-full) / 2);
	--spacing-quarter: calc(var(--spacing-full) / 4);
	--standard-border-radius: 4px;
	--fw-light: 300;
	--fw-medium: 400;
	--fw-semi-bold: 500;
	--fw-bold: 700;
	--fs-base: 1rem;
	--fs-sm: calc(var(--fs-base) * .875);
	--fs-lg: calc(var(--fs-base) * 1.25);
	--fs-h1: calc(var(--fs-base) * 2.5);
	--fs-h2: calc(var(--fs-base) * 2);
	--fs-h3: calc(var(--fs-base) * 1.75);
	--fs-h4: calc(var(--fs-base) * 1.5);
	--fs-h5: calc(var(--fs-base) * 1.25);
	--fs-h6: --fs-base;
	--toolbar-height: 56px;
}

*, *::before, *::after {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

html, body {
	width: 100%;
	height: 100%;
	font-size: 16px;
	font-family: "Roboto","Arial","Helvetica",sans-serif;
	line-height: 1.5;
}

#root,
.wrapper {
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	min-height: 100%;
	background: var(--color-grey-pale);
}

.wrapper {
	height: 100%;
}

main {
	flex-grow: 1; 
	/* to make footer always sticked to the bottom no matter eather there is content in main or no */
	min-height: 100vh;
}

header, main, footer {
	width: 100%;
	position: relative;
}

ul, ol, dl {
	list-style: none;
}

img {
	display: inline-block;
	max-width: 100%;
	height: auto;
}

label {
	display: inline-block;
}

a,
button {
	cursor: pointer;
}

a {
	color: #4062b9;
	text-decoration: none;
	display: inline-block;

	&:hover {
		text-decoration: underline;
	}
}

.modal {
	width:100%; 
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	background-color: rgba(0,0,0,.6);
	z-index: 100;
}

.clearfix::after {
	content: '';
	display: block;
	clear: both;
}

.mb-full {
	margin-bottom: var(--spacing-full);
}

.mt-full {
	margin-top: var(--spacing-full);
}

.mb-half {
	margin-bottom: var(--spacing-half);
}

.m-auto {
	margin: auto !important;
}