.NavigationItem {
  margin: 10px 0;
  display: block;
  width: 100%;
  position: relative;
  --border-height: 4px;

  a {
    color: var(--color-chocolate);
    position: relative;
    width: 100%;
    display: block;
    border-bottom: var(--border-height) solid transparent;

    &:hover {
      text-decoration: none;
    }
  }
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active {
  color: #40a4c8;
}

@media (min-width: 500px) {
  .NavigationItem {
    margin: 0;
    display: flex;
    width: auto;
    align-items: center;
  }

  .NavigationItem a {
    color: var(--color-white);
    padding: calc(var(--spacing-half) + var(--border-height)) 10px var(--spacing-half);
  }

  .NavigationItem a:hover,
  .NavigationItem a:active,
  .NavigationItem a.active {
    background-color: var(--color-chocolate);
    border-bottom: 4px solid #40a4c8;
    color: var(--color-white);
  }
}
.NavigationItems > .NavigationItem:first-of-type a:hover {
  border-bottom: 4px solid var(--color-red);
}
.NavigationItems > .NavigationItem:nth-of-type(2) a:hover {
  border-bottom: 4px solid blue;
}
.NavigationItems > .NavigationItem:nth-of-type(3) a:hover {
  border-bottom: 4px solid rgb(9, 231, 9);
}
.NavigationItems > .NavigationItem:nth-of-type(4) a:hover {
  border-bottom: 4px solid rgb(6, 108, 115);
}
.NavigationItems > .NavigationItem:last-of-type a:hover {
  border-bottom: 4px solid gold;
}

.NavigationItems {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-flow: column;
  align-items: center;
  height: 100%;
}

@media (min-width: 500px) {
  .NavigationItems {
    flex-flow: row;
  }
}

.submenu {
  display: none;
  position: absolute;
  top: 110%;
  right: 0;
  background-color: var(--color-silver);
  width: 220%;
  text-transform: uppercase;

  > li {
    border-bottom: 1px solid rgba(0, 0, 0, .12);
  }
}

.has-submenu {
  &.-active {
    > .submenu {
      display: block;
    }

    > a::after {
      transform: translateY(-50%) rotateZ(180deg);
    }
  }

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 100%;
  }

  > a {
    padding-right: 25px;

    &::after {
      content: '';
      background: url(../../../../../static_assets/images/arrow-down-white.svg) no-repeat center/contain;
      position: absolute;
      top: 53%;
      right: 4px;
      width: 20px;
      height: 10px;
      transform: translateY(-50%);
      transition: transform .5s linear;
    }
  }
}

.SideDrawer {
  .has-submenu {
    > a {
      &::after {
        background-image: url(../../../../../static_assets/images/arrow-down-grey.svg);
        top: 50%;
      }
    }
  }
}

  @media (max-width: 500px) {
    .submenu {
      position: static;
      width: 100%;
      background: none;
      list-style: square;
    }
    .has-submenu::before {
      content: none;
    }
    .has-submenu > a::after {
      color: var(--color-chocolate);
      top: 0;
      transform: none;
    }
  }