@use '../../../../styles//mixins/respond' as *;

$socialIcons: (
  'facebook',
  'youtube',
  'linkedin',
  'instagram',
);

.Footer {
    background-color: var(--footer-bg);
    color: var(--footer-text-color);
}

.footer-content {
  > .row-top,
  > .row-bottom {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    gap: var(--spacing-half);
    margin-bottom: var(--spacing-full);
  }

  > .row-top {
    > .about {
      > p {
        max-width: 420px;
      }
    }
  }

  > .row-bottom {
    padding-top: var(--spacing-full);
    align-items: center;
    text-align: center;
  }

  h6 {
    color: var(--color-white);
    text-transform: uppercase;
    padding-top: 5px;
    margin-bottom: var(--spacing-quarter);
    letter-spacing: 2px;
  }

  @include respond(mobile-large desktop) {
    > .row-top,
    > .row-bottom {
      flex-direction: row;
      flex-wrap: nowrap;
      row-gap: var(--spacing-full);

      > .about {
        width: 50%;
      }

      > .categories,
      > .quick-links {
        width: 25%;
      }
    }
  }
}

.footer-links {
  columns: 2;

  @include respond(mobile-large desktop) {
    columns: 1;
  }

  a {
    color: inherit;

    &:active,
    &:focus,
    &:hover {
      color:#3366cc;
      text-decoration:none;
    }
  }
}

.social-icons a {
  width: 44px;
  height: 44px;
  line-height: 44px;
  margin-left: var(--spacing-quarter);
  margin-right: var(--spacing-quarter);
  background-repeat: no-repeat;
  background-size: 25px auto;
  background-position: center;
  background-color: var(--bg-social);
  border-radius: 50%;
  transition:all .2s linear;

  @each $socialIcon in $socialIcons {
    &.-#{$socialIcon} {
      background-image: url(../../../../static_assets/images/social/#{$socialIcon}.svg);
      
      &:hover {
        background-color: var(--bg-#{$socialIcon});
      }
    }
  }

  &:active,
  &:focus,
  &:hover {
    color: var(--color-white);
  }
}

.social-icons {
  > li {
    display: inline-block;
    margin-bottom: 4px;

    &.-with-title {
      margin-right: 15px;
      text-transform: uppercase;
      font-weight: var(--fw-bold);
      font-size: var(--fs-sm);
    }
  }
}