@use '../../../../styles/mixins/position' as *;
@use '../../../../styles/mixins/respond' as *;

%letter-blocks {
	width: 35px; 
	height: 35px;
	line-height: 35px;
	text-align: center;
	margin: 5px;
}
%center {
	display: flex;
	justify-content: center;
	align-content: center;
	text: {
		align: center;
		transform: lowercase;
	}
}

.card-container{
	max-width: 768px;
	margin: auto;
	border-radius: var(--standard-border-radius);
	background-color: var(--color-white);
	box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);

	@include respond(desktop) {
		margin-top: var(--spacing-half);
	}
}

.card-content {
    padding: var(--spacing-quarter);
	@extend %center;
	flex-direction: column;

	> .word-explanation {
		text-transform: uppercase;
		max-width: 480px;
		margin: auto;
	}
}

.blank-letter {
	@extend %letter-blocks;
	background: var(--grey-bg);
	border-color: #aaa #ccc #ccc #aaa;
	font-size: 1.25rem;
	&.-correct {
		border-color: transparent transparent #46bf21;
		background: #92f366;
		color: #1a6608;
	}
	&.-wrong {
		background: #ff5b47;
		border-color: #ff8271 #ca3d2c #bd2917 #ff6e5b;
	}
	&:focus {
		outline: 2px ridge;
		outline-color: var(--letters-bg);
	}
}

.puzzle-set {
	margin: 10px 0;
}

.letter {
	@extend %letter-blocks;
	background: var(--letters-bg);
	color: var(--color-white);
	cursor: pointer;
	transition: width .5s linear, opacity 1s linear;

	&.-hidden {
		// visibility: hidden;
		width: 0;
		opacity: 0;
	}
}

.letters-set > .letter {
	display: inline-block;
	font-size: var(--fs-lg);
	margin: 5px;
	vertical-align: middle;
	text-shadow: 1px 1px rgba(black,0.7);
}

.card-controls {
	background: var(--grey-bg);
	
	@include respond(desktop) {
		padding: 20px;
	}

	> .additions {
        @extend %center;
		flex-direction: column-reverse;
        gap: var(--spacing-half);
		padding: var(--spacing-quarter);

        @include respond(mobile-large desktop) {
            flex-direction: row;
        }

        > .image-container {
            max-width: 320px;
			height: 200px;
            margin: auto;
			display: none;

			@include respond(mobile-large desktop) {
				display: block;
			}

			> img {
				height: 100%;
			}
        }
	}

	.controls {
		margin: var(--spacing-quarter);
        display: flex;
        flex-direction: column-reverse;

        @include respond(mobile-large desktop) {
            flex-direction: row;
            justify-content: center;
        }

		> .Button {
			margin: 3px;

			@include respond(mobile-large desktop) {
				margin: 10px;
			}
		}
	}

	.word-context {
		> h4 {
			margin-bottom: 10px;
		}
	}
}

.roster {
	padding: 20px;
	background: var(--old-paper-bg);
	list-style-type: square;
	font-weight: 400;
	font-style: italic;

	> li.-done {
		text-decoration: line-through;
	}
}

.modal-container {
	background: var(--color-white);
	text-align: center;
	padding: 15px;
	width: 90%;
	max-width: 640px;
	position: absolute;
	left: 50%;
	transform: translateX(-50%);

	> .closeModal {
		display: block;
		margin: 20px auto;
		border-radius: 30px;
		background-color: var(--main-color);
		cursor: pointer;
	}
}