@import 'mixins/respond';

html {
	overflow-x: hidden;
}

html,
body {
	min-height: 100%;
}

body {
	position: relative;
}

textarea {
	resize: vertical;
}

.wrap {
	margin: 0 auto;
	min-width: 320px;
	padding: 2em 2.5em;
	position: relative;
	text-align: left;

	@include respond(desktop) {
        width: clamp(1000px, 94%, 1296px);
	}
}

main {
	margin-top: var(--toolbar-height);
}

.mobile-small-show,
.mobile-medium-show,
.mobile-large-show {
	display: none !important;
}

@include respond(mobile) {
	.mobile-hide {
		display: none !important;
	}
}

@include respond(mobile-small) {
	.mobile-small-show {
		display: block !important;
	}

	.mobile-small-hide {
		display: none !important;
	}
}

@include respond(mobile-medium) {
	.mobile-medium-show {
		display: block !important;
	}

	.mobile-medium-hide {
		display: none !important;
	}
}

@include respond(mobile-large) {
	.mobile-large-show {
		display: block !important;
	}

	.mobile-large-hide {
		display: none !important;
	}
}

.hidden {
	display: none !important;
}

._hide-text { // for accessibility testing tools
	display: block;
	font-size: 1rem; // bigger size for accessibility (for testing tools)
	height: 0;
	overflow: hidden;
	width: 0;
}

.ov-hidden {
	overflow: hidden !important;
}

.ov-x-auto {
	overflow-x: auto !important;
}

.ov-y-auto {
	overflow-y: auto !important;
}

.block {
	display: block !important;
}

.inline-block {
    display: inline-block !important;
}

.float_right {
	float: right;
}

.float_left {
	float: left;
}

.clear {
	&::after {
		clear: both;
		content: '';
		display: table;
	}
}

.pos-r {
	position: relative;
}

.pos-a {
    position: absolute
}

.break-word {
	word-wrap: break-word;
}