.Blog {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.Article {
    padding: var(--spacing-full) var(--spacing-quarter) 24px;

    > h1 {
        text-transform: capitalize;
        margin-bottom: var(--spacing-full);
        text-align: center;
        letter-spacing: -.01rem;
        line-height: 1.2;
    }

    > .image-container {
        max-width: 600px;
        height: 400px;
        display: block !important;

        > img {
            height: 100%;
            width: 100%;
            object-fit: cover;
        }
    }

    > .short-description {
        color: var(--color-dark-grey);
        font-size: var(--fs-h5);
        text-align: center;
        margin: 0 auto var(--spacing-full);
        max-width: 740px;
        letter-spacing: -.01rem;
        line-height: 1.4;
    }

    > .publication-details {
        text-align: center;
        text-transform: uppercase;
        font-size: var(--fs-base);
        letter-spacing: 2px;
        margin-bottom: var(--spacing-half);

        > .creation-date,
        > .update-date {
            color: var(--color-dark-grey);
            margin-right: var(--spacing-half);
        }
    }

    hr {
        margin-bottom: var(--spacing-full);
    }
}