@use '../../../../styles/mixins/text-truncate' as *;
@use '../../../../styles/mixins/respond' as *;

.Logo {
  background-color: var(--color-white);
  border-radius: 6px;
  display: flex;
  gap: 1rem;
  height: 90%;
  padding: 8px;

  &:hover {
    text-decoration: none;
  }

  > p {
    @include text-truncate;

    color: initial;
    font-size: 1.15rem;
    font-weight: var(--fw-bold);
    line-height: 1.9;
    
    > .subdescription {
      display: none;

      @include respond(mobile-large desktop) {
        & {
          display: initial;
        }
      }
    }
  }

  > img {
    height: 100%;
  }
}