.rites-of-passage {
    background-color: #eedbdb;
    padding: 30px;
    scroll-behavior: smooth;
}
.rites-of-passage > article {
    columns: 3 320px;
}
.rites-of-passage p {
    margin: 10px;
    text-indent: 20px;
    font-size: 1.2rem;
    font-family: serif;
}
.rites-of-passage p::first-letter {
    font: 900 1.8rem cursive;
    padding: 5px;
}
.rites-of-passage h1, 
.rites-of-passage .subhead {
    text-align: center;
    width: 100%;
    column-span: all;
    font-variant: small-caps;
}
.rites-of-passage h1{
    font-size: 2rem;
}
.rites-of-passage .subhead {
    font-size: 1.35em;
}

.Task2 {
    margin: 50px 0 0;
    font-family: sans-serif;
}
.Task2 td,
.Task2 li {
    padding: 10px;
}
.Task2 li {
    list-style: upper-latin;
}
.task-description {
    font: italic 900 1.1rem cursive;
}