.DrawerToggleButton {
  width: 40px;
  display: flex;
  flex-flow: column;
  justify-content: space-around;
  align-items: center;
  padding: 10px 0;
  cursor: pointer;
  overflow: hidden;
}

.DrawerToggleButton > div {
  width: 90%;
  height: 3px;
  margin-bottom: 5px;
  border-radius: 5px;
  background-color: var(--color-black);
  transition: transform 1s ease;
}

.DrawerToggleButton:hover > div:first-child {
  transform: translateY(260%) rotate(45deg);
}

.DrawerToggleButton:hover > div:nth-child(2) {
  transform: translateX(-150%); 
}

.DrawerToggleButton:hover > div:last-child {
  transform: translateY(-260%) rotate(-45deg);
}

@media (min-width: 500px) {
  .DrawerToggleButton {
    display: none;
  }
}
