@use '../.././../../styles/mixins/position' as *;

.Video {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.video-holder {
    max-width: 854px;
    margin: auto;
    width: 100%;

    > .frame-holder {
        position: relative;
        height: 0;
        margin: auto;
        padding-bottom: 56.25%;

        > iframe {
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
        }
    }
}

.task-link {
    display: block;
    max-width: 200px;
    margin: 20px auto;
}
.QuoteContainer {
    max-width: 820px;
    padding: 30px;
    margin: 30px auto;
    background: #fff;
    box-shadow: 0 3px 4px 0 #000;
}
.Quote {
    margin-bottom: 20px;
    font-size: 1.4rem;
}
.Quote > .author {
    margin-bottom: 20px;
    font-style: italic;
}

.video-additional-content {
    max-width: 640px;
    margin: var(--spacing-full) auto;

    > h2 {
        margin-bottom: var(--spacing-half);
        text-align: center;
    }

    > .btn-wrap {
        text-align: center;
    }
}